.hello {
  display: flex;
  list-style: none;
  justify-content: center;
  align-items: center;
}

.hello li {
  color: rgb(39, 39, 39);
  font-family: "Noto Serif", serif;
  font-size: 50px;
  font-weight: bold;
  margin-right: 2px;
  animation: neonTxt 2.5s ease-in-out infinite alternate;
}

ul li:nth-child(2) {
  animation-delay: 0.5s;
}
ul li:nth-child(3) {
  animation-delay: 1s;
}
ul li:nth-child(4) {
  animation-delay: 1.5s;
}
ul li:nth-child(5) {
  animation-delay: 2s;
}

@keyframes neonTxt {
  0%,
  5% {
    color: rgb(209, 209, 209);
    text-shadow: 0 0 8px #fff;
  }
  20%,
  100% {
    color: rgb(39, 39, 39);
  }
  30%,
  100% {
    text-shadow: none;
  }
}

.login-box form a {
  position: relative;
  display: flex;
  justify-content: center;
  padding: 10px 20px;
  color: #d61f28;
  font-size: 16px;
  text-decoration: none;
  text-transform: uppercase;
  overflow: hidden;
  transition: 0.5s;
  margin-top: 40px;
  letter-spacing: 4px;
}

.login form a {
  color: #d61f28;
  font-size: 16px;
  text-decoration: none;
  overflow: hidden;
  transition: 0.5s;
  letter-spacing: 4px;
}

.login a:hover {
  background: #d61f28;
  color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 5px #d61f28, 0 0 25px #d61f28, 0 0 50px #d61f28,
    0 0 100px #d61f28;
}

.login a span {
  position: absolute;
  display: block;
}

.login a span:nth-child(1) {
  top: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #d61f28);
  animation: btn-anim1 1s linear infinite;
}

@keyframes btn-anim1 {
  0% {
    left: -100%;
  }
  50%,
  100% {
    left: 100%;
  }
}

.login a span:nth-child(2) {
  top: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, transparent, #d61f28);
  animation: btn-anim2 1s linear infinite;
  animation-delay: 0.25s;
}

@keyframes btn-anim2 {
  0% {
    top: -100%;
  }
  50%,
  100% {
    top: 100%;
  }
}

.login a span:nth-child(3) {
  bottom: 0;
  right: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg, transparent, #d61f28);
  animation: btn-anim3 1s linear infinite;
  animation-delay: 0.5s;
}

@keyframes btn-anim3 {
  0% {
    right: -100%;
  }
  50%,
  100% {
    right: 100%;
  }
}

.login a span:nth-child(4) {
  bottom: -100%;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(360deg, transparent, #d61f28);
  animation: btn-anim4 1s linear infinite;
  animation-delay: 0.75s;
}

@keyframes btn-anim4 {
  0% {
    bottom: -100%;
  }
  50%,
  100% {
    bottom: 100%;
  }
}

@media only screen and (max-width: 1200px) {
  .pikachu {
    display: none;
  }
